import React from 'react';
import { Route } from 'react-router-dom';
import AboutUsContainer from './components/AboutUs/AboutUsContainer';
import CartContainer from './components/Cart/CartContainer';

import ContentContainer from './components/Content/ContentContainer';
import DeliveryContainer from './components/Delivery/DeliveryContainer';
import DetailContainer from './components/Detail/DetailContainer';
import EventDetailContainer from './components/EventDetail/EventDetailContainer';
import FooterContainer from './components/Footer/FooterContainer';
import HeaderContainer from './components/Header/HeaderContainer';
import OrderContainer from './components/Order/OrderContainer';
import ProductContainer from './components/Product/ProductContainer';
import Salon from './components/Salons/Salon';
import { useCookies } from 'react-cookie';




const App = () =>  {
    
    

    return (

       <div>

        <HeaderContainer />
        <Route exact path = '/' 
          render  = { () => <ContentContainer /> } />
        <Route exact path = '/cart'
          render = { () => <CartContainer /> } />
        <Route exact path = '/aboutUs'
          render = { () => <AboutUsContainer /> } />
        <Route exact path = '/salon'
          render = { () => <Salon /> } />
        <Route exact path = '/delivery'
          render = { () => <DeliveryContainer /> } />
        <Route exact path = '/product/:detailID' 
          render = { () => <DetailContainer />} />
        <Route path = '/event/:eventID'
          render = { () => <EventDetailContainer /> } />
        <Route path = '/product/:cat/:subcatID' 
          render = { () => < ProductContainer/> }/>
        <Route path = '/cart/order'
          render = { () => <OrderContainer />} />

        <FooterContainer />

      </div>
      
    );
}
  


export default App;

